.Legend {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: fit-content;
}

.Legend:hover {
    cursor: pointer;
}

.Legend-icon {
    border-radius: var(--spacing-s);
    margin-right: var(--spacing);
}
/* badge */

.Avatar {
  width: 32px;
  height: 32px;
  margin-left: var(--spacing);
  margin-right: var(--spacing);
  overflow: hidden;
  font-size: var(--font-size-m);
  line-height: var(--font-height-m);
  text-transform: uppercase;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: var(--font-weight-medium);
}

.Avatar--primary {
  background: var(--primary);
  color: var(--white);
}

.Avatar--success {
  background: var(--success-light);
  color: var(--white);
}

.Avatar--alert {
  background: var(--alert-light);
  color: var(--white);
}

.Avatar--warning {
  background: var(--warning-light);
}

.Avatar--accent1 {
  background: var(--accent1-light);
  color: var(--white);
}

.Avatar--accent2 {
  background: var(--accent2-light);
  color: var(--white);
}

.Avatar--accent3 {
  background: var(--accent3-light);
  color: var(--white);
}

.Avatar--accent4 {
  background: var(--accent4-light);
  color: var(--white);
}
@keyframes backdrop-open {
  from { opacity: 0; }
  to { opacity: 60; }
}

@keyframes backdrop-close {
  from { opacity: 60; }
  to { opacity: 0; }
}

.Backdrop {
  background-color: color-mod(var(--inverse) a(0.8));
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  display: none;
  align-items: center;
  z-index: 999999;
  overflow: auto;
  flex-direction: column;
  justify-content: center;
  touch-action: none;
  visibility: hidden;
}

.Backdrop--open {
  display: flex;
  visibility: visible;
}

.Backdrop-animation--open {
  animation: backdrop-open 300ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

.Backdrop-animation--close {
  animation: backdrop-close 110ms cubic-bezier(0.2, 0, 1, 0.9);
}



/* badge */

.Badge {
	margin-left: var(--spacing);
	margin-right: var(--spacing);
	/* 160px */
	max-width: calc(var(--spacing-2) * 10);
	/* truncate */
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: var(--font-size-s);
	line-height: var(--font-height-s);
	text-transform: uppercase;
	padding-right: var(--spacing-m);
	padding-left: var(--spacing-m);
	padding-top: var(--spacing-s);
	padding-bottom: var(--spacing-s);
	border-radius: var(--spacing-m);
	display: inline-block;
	box-sizing: border-box;
	font-weight: var(--font-weight-bold);
	cursor: default;
}

.Badge--primary {
	background: var(--primary);
	color: var(--white);
}

.Badge--secondary {
	background: var(--secondary);
}

.Badge--success {
	background: var(--success);
	color: var(--white);
}

.Badge--alert {
	background: var(--alert);
	color: var(--white);
}

.Badge--warning {
	background: var(--warning);
}

.Badge--accent1 {
	background: var(--accent1);
	color: var(--white);
}

.Badge--accent2 {
	background: var(--accent2);
	color: var(--white);
}

.Badge--accent3 {
	background: var(--accent3);
	color: var(--white);
}

.Badge--accent4 {
	background: var(--accent4);
}

.Badge--subtle-primary {
	color: var(--primary-darker);
	background: var(--primary-lightest);
}

.Badge--subtle-secondary {
	color: var(--secondary-darker);
	background: var(--secondary-lightest);
}

.Badge--subtle-success {
	color: var(--success-darker);
	background: var(--success-lightest);
}

.Badge--subtle-alert {
	color: var(--alert-darker);
	background: var(--alert-lightest);
}

.Badge--subtle-warning {
	color: var(--warning-darker);
	background: var(--warning-lightest);
}

.Badge--subtle-accent1 {
	color: var(--accent1-darker);
	background: var(--accent1-lightest);
}

.Badge--subtle-accent2 {
	color: var(--accent2-darker);
	background: var(--accent2-lightest);
}

.Badge--subtle-accent3 {
	color: var(--accent3-darker);
	background: var(--accent3-lightest);
}

.Badge--subtle-accent4 {
	color: var(--accent4-darker);
	background: var(--accent4-lightest);
}

/* breadcrumbs */

.BreadcrumbsWrapper {
    display: flex;
    flex-direction: column;

    box-sizing: border-box;
    background: var(--white);

    padding-left: var(--spacing-l);
    padding-right: var(--spacing-l);
    padding-top: var(--spacing-2);
    padding-bottom: var(--spacing-2);
}

.BreadcrumbsWrapper-list {
    display: flex;
    margin: 0;
    padding: 0;
    flex-direction: row;
}

.Breadcrumb {
    display: flex;
    font-size: var(--font-size-s);
}

.Breadcrumb::after {
    content: "/";
    margin-left: var(--spacing-m);
    margin-right: var(--spacing-m);
    color: var(--inverse-light);
}

.Breadcrumb-link {
    color: var(--inverse-light);
    cursor: pointer;
}
/* button */

.Button {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    
    vertical-align: middle;
    cursor: pointer;
    user-select: none;
    border: 0;
    border-radius: var(--spacing-m);

    padding-top: var(--spacing);
    padding-bottom: var(--spacing);

    text-align: center;
    text-transform: capitalize;
    font-family: var(--font-family);
    font-weight: var(--font-weight-normal);
    font-size: var(--font-size);
    line-height: var(--font-height-s);
    color: var(--text-white);
	
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.Button:focus {
    outline: 0;
}

.Button--iconAlign-right {
    flex-direction: row-reverse;
}

.Button--tiny {
    height: 24px;
    padding-right: var(--spacing);
    padding-left: var(--spacing);
    padding-top: var(--spacing-m);
    padding-bottom: var(--spacing-m);
}

.Button--tiny.Button--square {
    padding-left: var(--spacing-m);
    padding-right: var(--spacing-m);
}

.Button--regular {
    height: 32px;
    padding-right: var(--spacing-l);
	padding-left: var(--spacing-l);
}

.Button--regular.Button--square {
    padding-left: var(--spacing);
    padding-right: var(--spacing);
}

.Button--large {
    height: 40px;
    padding-right: var(--spacing-2);
    padding-left: var(--spacing-2);
    
    line-height: var(--font-height-m);
    font-size: var(--font-size-m);
}

.Button--large.Button--square {
    padding-left: var(--spacing);
    padding-right: var(--spacing);
}

.Button--tiny.Button--square .Button-icon,
.Button--large.Button--square .Button-icon,
.Button--tiny.Button--square .Spinner,
.Button--large.Button--square .Spinner {
    padding-left: var(--spacing-s);
    padding-right: var(--spacing-s);
}

.Button--expanded {
    width: 100%;
}

.Button-icon {
    line-height: inherit;
    display: flex;
    align-items: center;
}

.Button-icon--left {
    margin-right: var(--spacing);
}

.Button-icon--right {
    margin-left: var(--spacing);
}

.Button--tiny .Button-icon--left {
    margin-right: var(--spacing-m);
}
.Button--tiny .Button-icon--right {
    margin-left: var(--spacing-m);
}

.Button--basic {
	background: var(--secondary-light);
	color: var(--inverse);
}

.Button--basic:hover {
	background: var(--secondary);
}

.Button--basic:active {
	background: var(--secondary-dark);
}

.Button--basic:focus {
    box-shadow: var(--shadow-spread) var(--secondary-shadow);
}

.Button--basic:disabled {
    background: var(--secondary-lighter);
    color: var(--inverse-lightest);
}

.Button--primary {
	background: var(--primary);
}

.Button--primary:hover {
	background: var(--primary-dark);
}

.Button--primary:active {
	background: var(--primary-darker);
}

.Button--primary:focus {
    box-shadow: var(--shadow-spread) var(--primary-shadow);
}

.Button--primary:disabled {
    background: var(--primary-lighter);
}

.Button--success {
	background: var(--success);
}

.Button--success:hover {
	background: var(--success-dark);
}

.Button--success:active {
	background: var(--success-darker);
}

.Button--success:focus {
    box-shadow: var(--shadow-spread) var(--success-shadow);
}

.Button--success:disabled {
    background: var(--success-lighter);
}

.Button--alert {
	background: var(--alert);
}

.Button--alert:hover {
	background: var(--alert-dark);
}

.Button--alert:active {
	background: var(--alert-darker);
}

.Button--alert:focus {
    box-shadow: var(--shadow-spread) var(--alert-shadow);
}

.Button--alert:disabled {
    background: var(--alert-lighter);
}

.Button--transparent {
	background: transparent;
	color: var(--inverse);
}

.Button--transparent:hover {
	background: var(--secondary-light);
}

.Button--transparent:active {
	background: var(--secondary);
}

.Button--transparent:focus {
    background: color-mod(var(--stone-light) a(0.2));
    /* box-shadow: var(--shadow-spread) var(--secondary-shadow); */
}

.Button--transparent:disabled {
    background: transparent;
    color: var(--inverse-lightest);
}

.Button .Spinner {
    margin-right: var(--spacing);
}

.Button--square .Spinner {
    margin-right: 0 !important;
}

.Button--tiny .Spinner {
    margin-right: var(--spacing-m);
}

.Button--tiny .Spinner--small {
    height: var(--font-size-s);
    width: var(--font-size-s);
}

.Button--large .Spinner--small {
    height: var(--font-size-l);
    width: var(--font-size-l);
}
/* calendar */

.Calendar-wrapper {
    display: flex;
    flex-wrap: wrap;
}

.Calendar {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    min-width: 330px;
}

.Calendar-header {
    display: flex;
    padding-left: var(--spacing-2);
    padding-right: var(--spacing-2);
    padding-top: var(--spacing-l);
    padding-bottom: var(--spacing);
}

.Calendar-headerIcon {
    cursor: pointer;
    user-select: none;
}

.Calendar-headerIcon--disabled .Icon {
    color: var(--text-disabled);
    pointer-events: none;
}

.Calendar-headerContent {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    white-space: nowrap;
    cursor: pointer;
    padding-top: var(--spacing-m);
    padding-bottom: var(--spacing-m);
    user-select: none;
}

.Calendar-headerContent--noIcon-left {
    margin-left: var(--spacing-3);
}

.Calendar-headerContent--noIcon-right {
    margin-right: var(--spacing-3);
}

.Calendar-body {
    display: flex;
    flex-direction: column;
    padding-left: var(--spacing-3);
    padding-right: var(--spacing-3);
    padding-bottom: var(--spacing-xl);
}

.Calendar-valueRow {
    display: flex;
    margin-top: var(--spacing);
    margin-bottom: var(--spacing);
    justify-content: center;
}

.Calendar-valueRow:first-child {
    margin-top: 0;
}

.Calendar-valueRow:last-child {
    margin-bottom: 0;
}

.Calendar-value {
    cursor: pointer;
    user-select: none;
    border-radius: var(--spacing-m);
}

.Calendar-value:first-child {
    margin-left: 0;
}

.Calendar-value:last-child {
    margin-right: 0;
}

.Calendar-value:hover {
    background: var(--secondary);
}

.Calendar-value--inRange,
.Calendar-value--inRange:hover {
    background: var(--primary-lightest);
    border-radius: 0;
    padding-top: var(--spacing-s);
    padding-bottom: var(--spacing-s);
}

.Calendar-value--inRange-error,
.Calendar-value--inRange-error:hover {
    background: var(--alert-lightest);
    border-radius: 0;
    padding-top: var(--spacing-s);
    padding-bottom: var(--spacing-s);
}

.Calendar-value--active,
.Calendar-value--active:hover {
    background: var(--primary);
}

.Calendar--year .Calendar-value,
.Calendar--month .Calendar-value {
    padding-top: var(--spacing);
    padding-bottom: var(--spacing);
    padding-left: var(--spacing-l);
    padding-right: var(--spacing-l);
    margin-left: var(--spacing-2);
    margin-right: var(--spacing-2);
}

.Calendar--date {
    --value-width: var(--spacing-4);
}

.Calendar--date .Calendar-valueRow {
    margin-top: var(--spacing-s);
    margin-bottom: var(--spacing-s);
}

.Calendar--date .Calendar-value {
    padding-top: var(--spacing);
    padding-bottom: var(--spacing);
    width: var(--value-width);
    text-align: center;
}

.Calendar-value--dummy {
    pointer-events: none;
}

.Calendar-value--disabled {
    pointer-events: none;
}

.Calendar-dayValues {
    display: flex;
    justify-content: center;
}
/* badge */

.Card {
  border-radius: var(--spacing-m);
  border: var(--border);
}

.Card--shadow-light {
  border: none;
  box-shadow: var(--shadow-s);
}

.Card--shadow-medium {
  border: none;
  box-shadow: var(--shadow-m);
}

.Card--shadow-dark {
  border: none;
  box-shadow: var(--shadow-l);
}

/* badge */

.Checkbox {
    display: flex;
    cursor: pointer;
    align-items: center;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.Checkbox .Checkbox-input {
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    margin: 0;
}


/* Type: Unchecked  */

.Checkbox-wrapper {
    top: 0;
    left: 0;
    border: var(--border);
    border-radius: var(--spacing-s);
    margin-right: var(--spacing);
    display: flex;
    align-items: center;
}

.Checkbox-wrapper--regular {
    height: var(--spacing-2);
    min-width: var(--spacing-2);
}

.Checkbox-wrapper--tiny {
    height: var(--spacing);
    min-width: var(--spacing);
}

.Checkbox-wrapper:hover {
    border: var(--spacing-xs) solid var(--inverse-lightest);
    background-color: var(--shadow-0);
}

.Checkbox-wrapper:active {
    border: var(--spacing-xs) solid var(--inverse-lightest);
    background-color: var(--secondary-light);
}

.Checkbox-wrapper:focus {
    border: var(--border);
    background-color: var(--shadow-0);
}

.Checkbox--disabled .Checkbox-wrapper {
    border: var(--spacing-xs) solid var(--secondary-light);
    background-color: var(--secondary-lightest);
    pointer-events: none;
}


/* Type: checked && Type: indeterminate  */

.Checkbox-wrapper--checked,
.Checkbox-wrapper--indeterminate {
    background-color: var(--primary);
    border: 0;
}

.Checkbox-wrapper--checked:hover,
.Checkbox-wrapper--indeterminate:hover {
    background-color: var(--primary-dark);
    border: 0;
}

.Checkbox-wrapper--checked:active,
.Checkbox-wrapper--indeterminate:active {
    background-color: var(--primary-darker);
    border: 0;
}

.Checkbox-wrapper--checked:focus,
.Checkbox-wrapper--indeterminate:focus {
    background-color: var(--primary);
    border: 0;
}

.Checkbox--disabled .Checkbox-wrapper--checked,
.Checkbox--disabled .Checkbox-wrapper--indeterminate {
    background-color: var(--primary-lighter);
    border: 0;
    pointer-events: none;
}
/* Donut Chart */

.DonutChart {
    height: 100%;
}

.DonutChart-legends {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: var(--spacing-l) 0;
}

.DonutChart-tooltip {
    color: var(--white);
    padding: var(--spacing) var(--spacing-2);
    border-radius: var(--spacing-m);
    background: var(--inverse);
}
.Dropdown {
    position: relative;
    min-width: var(--spacing-7);
    width: fit-content;
}

.Dropdown-placeholder .PopperWrapper-trigger {
    width: 100%;
}

.Dropdown-input .Input {
    min-width: unset;
}

.Dropdown-subinfo {
    font-size: var(--spacing-l);
    color: var(--inverse-light);
    margin-left: var(--spacing-l);
    margin-top: var(--spacing);
    margin-bottom: 6px;
}

.Dropdown-border {
    border-top: var(--spacing-xs) solid var(--secondary-light);
}

.Dropdown-errorWrapper {
    min-height: var(--spacing-7);
}

.Dropdown-buttonWrapper {
    display: flex;
    justify-content: flex-end;
    padding-top: var(--spacing);
    padding-right: var(--spacing-l);
    padding-bottom: var(--spacing);
    border-top: var(--spacing-xs) solid var(--secondary-light);
}

.Option-wrapper,
.Option-loadingWrapper,
.Dropdown-wrapper .ListCheckbox-parentWrapper,
.Dropdown-wrapper .ListCheckbox-childWrapper {
    display: flex;
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: var(--spacing-l);
    padding-right: var(--spacing-l);
}

.Option-wrapper:hover,
.Dropdown-wrapper .ListCheckbox-parentWrapper:hover,
.Dropdown-wrapper .ListCheckbox-childWrapper:hover {
    background-color: var(--secondary-lightest);
    cursor: pointer;
}

.Dropdown-wrapper .ListCheckbox-parentWrapper,
.Dropdown-wrapper .ListCheckbox-childWrapper--top {
    padding-top: 10px;
}

.Dropdown-wrapper .ListCheckbox-childWrapper:last-child {
    padding-bottom: 10px;
}

.Dropdown-wrapper .ListCheckbox-childWrapper .Checkbox {
    overflow: hidden;
}

.Dropdown-wrapper-loaderWrapper {
    margin-top: var(--spacing-s);
    margin-bottom: var(--spacing-s);
}

.Option-loadingWrapper {
    pointer-events: none;
}

.Option-wrapper--selected {
    background-color: var(--primary);
    pointer-events: none;
}

.Option-wrapper--top {
    padding-top: 10px;
}

.Option-wrapper--bottom {
    padding-bottom: 10px;
}

.Option-label {
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.Option-text,
.Dropdown-wrapper .ListCheckbox .Checkbox .Text {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.Option-text--wrap,
.Dropdown-wrapper--wrap.Dropdown-wrapper .ListCheckbox .Checkbox .Text {
    white-space: unset;
}

.Dropdown-wrapper--wrap.Dropdown-wrapper .ListCheckbox .Checkbox {
    align-items: flex-start;
}

.Option-wrapper--icon {
    padding-left: var(--spacing);
}

.Option-icon {
    margin-top: 3px;
}

.Option-subinfo {
    font-size: var(--spacing-l);
    color: var(--inverse-light);
}

.Scroller-wrapper::-webkit-scrollbar,
.ListCheckbox-scroller::-webkit-scrollbar {
    width: 10px;
}

.Scroller-wrapper::-webkit-scrollbar-thumb,
.ListCheckbox-scroller::-webkit-scrollbar-thumb {
    background: var(--secondary);
    border-radius: 10px;
}
.DropdownButton {
    min-width: var(--spacing-7);
    justify-content: space-between;
    align-items: center;
    padding-top: unset;
    padding-bottom: unset;
}

.DropdownButton-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    overflow: hidden;
}

.DropdownButton--tiny {
    height: var(--spacing-xl);
    padding-right: var(--spacing);
    padding-left: var(--spacing);
}

.DropdownButton--regular {
    height: var(--spacing-3);
    padding-right: var(--spacing);
    padding-left: var(--spacing-l);
}

.DropdownButton:focus {
    background-color: var(--secondary-lighter);
}

.DropdownButton:disabled {
    pointer-events: none;
}

.DropdownButton--icon {
    padding-left: var(--spacing);
}

.DropdownButton-label {
    color: var(--inverse-light);
    margin-right: var(--spacing);
}

.DropdownButton-text {
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.DropdownButton--placeholder {
    color: var(--inverse-light);
}

.DropdownButton--label {
    color: var(--inverse);
}

.DropdownButton--moreIcon {
    padding-right: unset;
    padding-left: unset;
    justify-content: center;
}

.DropdownButton--moreIcon.DropdownButton--regular {
    min-width: var(--spacing-3);
}

.DropdownButton--moreIcon.DropdownButton--tiny {
    min-width: var(--spacing-xl);
}

/* Although the icons in the font can be scaled to any size, in accordance with material design icons guidelines, 
 * we recommend them to be shown in either 18, 24, 36 or 48px. The default being 24px. 
 * https://google.github.io/material-design-icons/
 */


/* Rules for sizing the icon. */


/* .material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; } */


/* Rules for using icons as black on a light background. */


/* .material-icons.md-dark { color: rgba(0, 0, 0, 0.54); }
.material-icons.md-dark.md-inactive { color: rgba(0, 0, 0, 0.26); } */


/* Rules for using icons as white on a dark background. */


/* .material-icons.md-light { color: rgba(255, 255, 255, 1); }
.material-icons.md-light.md-inactive { color: rgba(255, 255, 255, 0.3); } */
.Icon {
  overflow: hidden;
}

.Icon--subtle {
  color: var(--text-subtle);
}

.Icon--disabled {
  color: var(--text-disabled);
}

.Icon--destructive {
  color: var(--text-destructive);
}

.Icon--white {
  color: var(--text-white);
}

.Icon--default {
  color: var(--text);
}

.Icon--info {
  color: var(--primary);
}

.Icon--success {
  color: var(--success);
}

.Icon--warning {
  color: var(--warning);
}

.Icon--alert {
  color: var(--alert);
}
/* input */

.Input-input::-ms-clear {
    display: none;
}

.Input {
    display: flex;
    flex-direction: column;
    min-width: var(--spacing-9);
}

.Input-wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
    border-radius: var(--spacing-m);
    border: var(--border);
    padding-right: var(--spacing-l);
    padding-left: var(--spacing-l);
}

.Input-wrapper--tiny {
    height: var(--spacing-xl);
    padding-top: var(--spacing-m);
    padding-bottom: var(--spacing-m);
}

.Input-wrapper--regular {
    height: var(--spacing-3);
    padding-top: var(--spacing);
    padding-bottom: var(--spacing);
}

.Input-wrapper--large {
    height: 40px;
}

.Input-wrapper--large .Input-input {
    line-height: var(--font-height-m);
    font-size: var(--font-size-m);
}

.Input-wrapper:focus-within {
    border: var(--spacing-xs) solid var(--primary);
}

.Input-wrapper:focus-within .Input-icon--left .Icon {
    color: var(--primary);
}

.Input-wrapper--error:focus-within {
    border: var(--spacing-xs) solid var(--alert);
    box-shadow: var(--shadow-spread) var(--alert-shadow);
}

.Input-wrapper--error:focus-within .Input-icon--left .Icon {
    color: var(--alert);
}

.Input-wrapper--disabled {
    background: var(--secondary-lightest);
    border-color: var(--secondary-light);
}

.Input-wrapper--disabled .Input-icon--left .Icon{
    color: var(--inverse-lightest);
}

.Input-wrapper--error {
    border: var(--spacing-xs) solid var(--alert);
}

.Input-input {
    display: flex;
    width: 100%;
    font-family: var(--font-family);
    font-weight: var(--font-weight-normal);
    font-size: var(--font-size);
    line-height: var(--font-height-s);
    color: var(--night);
    padding: 0;
    border: none;
}

.Input-input::placeholder {
    color: var(--inverse-light);
}

.Input-input:focus {
    outline: 0;
}

.Input-input:disabled {
    background: var(--secondary-lightest);
}

.Input-input:disabled .Input-input::placeholder {
    color: var(--inverse-light);
}

.Input-icon {
    display: flex;
    align-content: center;
    font-size: var(--font-size-m);
}

.Input-icon--left .Icon {
    margin-right: var(--spacing);
    color: var(--inverse);
}

.Input-icon--disabled .Icon{
    color: var(--inverse-lightest);
}

.Input-icon--right .Icon{
    cursor: pointer;
    margin-left: var(--spacing);
    color: var(--inverse-lightest);
}

.Input-icon--error {
    margin-right: var(--spacing-m);
    font-size: var(--font-size);
}

.Input-caption {
    display: flex;
    align-items: center;
    margin-top: var(--spacing-m);
}

.Input-label {
    display: flex;
    margin-bottom: var(--spacing-m);
}

.Input-label span {
    height: var(--spacing-m);
    width: var(--spacing-m);
    border-radius: 50%;
    background: var(--alert);
    margin-left: var(--spacing-m);
}

.Input-inlineLabel {
    margin-right: var(--spacing);
}
.Message {
    background: var(--white);
    border-radius: var(--spacing-m);
    box-shadow: var(--shadow-m);
    padding-top: var(--spacing-l);
    padding-right: var(--spacing-xl);
    padding-bottom: var(--spacing-l);
    padding-left: var(--spacing-2);
    display: flex;
    flex-direction: row;
    overflow: hidden;
    border-top: var(--border-s);
    min-height: var(--spacing-5);
    width: 280px;
    box-sizing: border-box;
  }
  
  .Message--alert {
    border-color: var(--alert);
  }
  
  .Message--success {
    border-color: var(--success);
  }
  
  .Message--info {
    border-color: var(--primary);
  }
  
  .Message--warning {
    border-color: var(--warning);
  }
  
  .Message-icon {
    margin-right: var(--spacing-2);
    padding-top: var(--spacing-s);
    width: var(--spacing-2);
  }

  .Message-icon--withTitle {
    padding-top: var(--spacing-m);
  }
  
  .Message-title {
    margin-bottom: var(--spacing);
    
  }
 
@keyframes modal-open {
  from {
    top: calc(10% + 20px);
    opacity: 0;
  }
  to {
    top: 10%;
    opacity: 100;
  }
}

@keyframes modal-close {
  from {
    top: calc(10%);
    opacity: 100;
  }
  to {
    top: calc(10% + 20px);
    opacity: 0;
  }
}

.Modal-container {
  display: flex;
  position: fixed;
  flex-direction: row;
  justify-content: center;
  width: 100vw;
  z-index: 99999999;
  top: 0;
}

.Modal {
  position: fixed;
  top: 10%;
  min-width: 480px;
  max-width: 960px;
  min-height: 200px;
  box-shadow: var(--shadow-m);
  background-color: var(--white);
  border-radius: var(--spacing-m);
  overflow: auto;
  display: none;
  visibility: hidden;
}

.Modal--open {
  display: block;
  visibility: visible;
}

.Modal-animation--open {
  animation: modal-open 300ms cubic-bezier(0.4, 0.14, 0.3, 1);
}

.Modal-animation--close {
  animation: modal-close 150ms cubic-bezier(0.4, 0.14, 0.3, 1);
}

.Modal-close-icon {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  right: var(--spacing-2);
}

.Modal--small {
  width: 480px;
}

.Modal--medium {
  width: 640px;
}

.Modal--large {
  width: 800px;
}

.Modal-header {
  display: flex;
  padding: var(--spacing-2) var(--spacing-2) 0;
  justify-content: flex-start;
  align-items: center;
}

.Modal-header-subheader {
  padding-left: var(--spacing-2);
}

.Modal-header-subheader--withIcon {
  padding-left: var(--spacing-4);
}

.Modal-header-wrapper {
  margin-bottom: var(--spacing-l);
}

.Modal-header-icon {
  display: flex;
  align-items: center;
  margin-right: var(--spacing-2);
}

.Modal-description {
  padding: var(--spacing-2) 0;
}

.Modal-footer {
  padding: var(--spacing-2);
  display: flex;
  justify-content: flex-end;
  flex-grow: 0;
}

.Modal-footer > * {
  margin-left: var(--spacing);
}

.Modal-body {
  padding: var(--spacing-2);
  max-height: 480px;
  overflow-y: scroll;
}

.Modal-body--border {
  border-top: var(--border);
  border-bottom: var(--border);
}
/* pagination */

.Pagination {
    display: flex;
    vertical-align: middle;
    flex-direction: row;
    flex-wrap: wrap;
}

.Pagination .Button {
    width: var(--spacing-5);
    padding-left: 20px;
    padding-right: 20px;
}

.Pagination-buttonWrapper {
    display: flex;
    flex-direction: row;
}

.Pagination-pageIndex {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: var(--spacing-xl);
    padding-right: var(--spacing-xl);
}

.Pagination-pageIndex .Input {
    min-width: var(--spacing-3);
    width: var(--spacing-5);
    margin-right: var(--spacing);
}

.Pagination-pageIndex .Input-input {
    text-align: center;
}

.Pagination-pageIndex .Input-input::-webkit-inner-spin-button,
.Pagination-pageIndex .Input-input::-webkit-outer-spin-button {
    appearance: none;
    margin: 0;
}

.Pagination-pageIndex .Input-input {
    -moz-appearance: textfield;
}

@media (max-width: 575px) {
    .Pagination-pageIndex {
        order: -1;
        flex-basis: 100%;
    }
    .Pagination-buttonWrapper {
        margin-top: var(--spacing);
        width: 50%;
    }
    .Pagination-buttonWrapper--next {
        justify-content: flex-start;
    }
    .Pagination-buttonWrapper--previous {
        justify-content: flex-end;
    }
}
.Pills {
    margin-left: var(--spacing);
	margin-right: var(--spacing);
	/* 160px */
	max-width: calc(var(--spacing-2) * 10);
	/* truncate */
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: var(--font-size-s);
	line-height: var(--font-height-s);
    text-transform: uppercase;
    padding-right: 6px;
    padding-left: 6px;
	padding-top: var(--spacing-s);
	padding-bottom: var(--spacing-s);
	border-radius: 20px;
	display: inline-block;
	box-sizing: border-box;
	font-weight: var(--font-weight-bold);
	cursor: default;
}
@keyframes shimmer {
  0%{
    background-position: -500px 0
  }
  100%{
    background-position: 500px 0
  }
}

.Placeholder--animation {
  border-radius: var(--spacing-m);
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: shimmer;
  animation-timing-function: linear;
  background: var(--secondary-lightest);
  background: linear-gradient(to right, var(--secondary-lighter) 8%, var(--secondary-dark) 18%, var(--secondary-lighter) 33%);
  background-size: 1000px 104px;
}

.Placeholder {
  display: block;
}

.Placeholder--withImage {
  display: flex;
  justify-content: flex-start;
}

.Placeholder-paragraph {
  height: 16px;
  margin-bottom: 8px;
}

.Placeholder-paragraph:last-child {
  margin-bottom: 0;
}

.Placeholder-paragraph--small {
  width: 33%;
}

.Placeholder-paragraph--medium {
  width: 60%;
}

.Placeholder-paragraph--large {
  width: 100%;
}

.Placeholder-image {
  width: 16px;
  height: 16px;
  display: inline-block;
}

.Placeholder-image--small {
  width: 16px;
  height: 16px;
}

.Placeholder-image--medium {
  width: 32px;
  height: 32px;
}

.Placeholder-image--large {
  width: 48px;
  height: 48px;
}

.Placeholder-image--round {
  border-radius: 50%;
}

.Popover {
  border-radius: var(--spacing-m);
  position: absolute;
  z-index: 999999;
  background: var(--white);
  box-shadow: var(--shadow-m);
}

.Popover--dark {
  background: var(--inverse);
  color: var(--white);
}
/* Radio */

.Radio {
    display: flex;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    align-items: center;
}

.Radio-outerWrapper {
    position: relative;
    margin-right: var(--spacing);
}

.Radio-outerWrapper--regular {
    height: var(--spacing-2);
    width: var(--spacing-2);
}

.Radio-outerWrapper--tiny {
    height: var(--spacing-l);
    width: var(--spacing-l);
}

.Radio-input {
    opacity: 0;
    cursor: pointer;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 2;
}

.Radio-wrapper {
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    position: absolute;
    border: var(--border);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    z-index: 1;
}

.Radio--disabled .Radio-wrapper {
    border: var(--spacing-xs) solid var(--secondary-light);
    background-color: var(--secondary-lightest);
    pointer-events: none;
}

.Radio-input:checked~.Radio-wrapper {
    border: var(--spacing-xs) solid var(--primary);
}

.Radio-input:checked~.Radio-wrapper:focus {
    border: var(--spacing-xs) solid var(--primary-dark);
}

.Radio-input:checked~.Radio-wrapper:after {
    display: block;
}

.Radio-wrapper:after {
    content: "";
    display: none;
    border-radius: 50%;
    background: var(--primary);
}

.Radio-wrapper--regular:after {
    width: var(--spacing);
    height: var(--spacing);
}

.Radio-wrapper--tiny:after {
    width: var(--spacing-m);
    height: var(--spacing-m); 
    border: var(--spacing-xs) solid var(--primary);
}

.Radio--disabled .Radio-input:checked~.Radio-wrapper {
    border: var(--spacing-xs) solid var(--primary-lighter);
    pointer-events: none;
}

.Radio--disabled .Radio-input:checked~.Radio-wrapper:after {
    background: var(--primary-lighter);
}

.Radio--disabled .Radio-wrapper--checked {
    border: var(--spacing-xs) solid var(--primary-lighter);
    pointer-events: none;
}
.RangePicker {

}

.RangePicker-input {
    box-sizing: border-box;
}

.RangePicker-input--startDate {
    padding-right: var(--spacing-m);
}

.RangePicker-input--endDate {
    padding-left: var(--spacing-m);
}

@media (max-width: 576px) {
    .RangePicker-input {
        padding: 0;
    }

    .RangePicker-input--endDate {
        padding-top: var(--spacing);
    }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px;
  }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px;
  }
}

.Spinner {
  animation: rotate 2s linear infinite;
}

.Spinner--small {
  height: var(--spacing-2);
  width: var(--spacing-2);
}

.Spinner--medium {
  height: var(--spacing-3);
  width: var(--spacing-3);
}

.Spinner--large {
  height: var(--spacing-4);
  width: var(--spacing-4);
}

.Circle {
  stroke-linecap: round;
  stroke-dasharray: 1 200;
  stroke-dashoffset: 0;
  animation: spin 1.5s ease-in-out infinite;
}

.Circle--primary {
  stroke: var(--primary);
}

.Circle--secondary {
  stroke: var(--secondary-dark);
}

.Circle--white {
  stroke: var(--white);
}

.StatusHints {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: fit-content;
}

.StatusHints-icon {
    border-radius: 50%;
    height: var(--spacing);
    width: var(--spacing);
    margin-right: var(--spacing);
}

.StatusHints--alert {
    background: var(--alert);
}

.StatusHints--success {
    background: var(--success);
}

.StatusHints--warning {
    background: var(--warning);
}

.StatusHints--info {
    background: var(--primary);
}

.StatusHints--default {
    background: var(--secondary);
}
.Switch {
    display: flex;
}

.Switch--tiny {
    height: var(--spacing-2);
    width: var(--spacing-3);
}

.Switch--regular {
    height: var(--spacing-xl);
    width: var(--spacing-4);
}

.Switch--large {
    height: var(--spacing-3);
    width: var(--spacing-5);
}

.Switch-input {
    opacity: 0;
    width: 0;
    height: 0;
}

.Switch-wrapper {
    cursor: pointer;
    display: flex;
    align-items: center;
    line-height: 100%;
    padding: var(--spacing-xs);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--secondary-light);
}

.Switch-wrapper--tiny {
    border-radius: 10px;
}

.Switch-wrapper--regular {
    border-radius: 34px;
}

.Switch-wrapper--large {
    border-radius: 38px;
}

.Switch-wrapper:before {
    content: "";
    height: 100%;
    width: 50%;
    background-color: var(--shadow-0);
    box-shadow: 0 0 var(--spacing-xs) 0 var(--shadow-20);
    border-radius: 50%;
    box-sizing: border-box;
}

.Switch-wrapper--checked.Switch-wrapper--primary {
    background-color: var(--primary);
}

.Switch-wrapper--checked.Switch-wrapper--alert {
    background-color: var(--alert);
}

.Switch-wrapper--checked.Switch-wrapper--success {
    background-color: var(--success);
}

.Switch-wrapper--checked.Switch-wrapper--warning {
    background-color: var(--warning);
}

.Switch-wrapper--checked:before {
    -webkit-transform: translateX(100%);
    -ms-transform: translateX(100%);
    transform: translateX(100%);
}

.Switch--disabled {
    pointer-events: none;
}

.Switch-wrapper--disabled:before {
    background-color: var(--secondary-lightest);
}

.Switch-wrapper--checkedDisabled.Switch-wrapper--primary {
    background-color: var(--primary-lighter);
}

.Switch-wrapper--checkedDisabled.Switch-wrapper--alert {
    background-color: var(--alert-lighter);
}

.Switch-wrapper--checkedDisabled.Switch-wrapper--success {
    background-color: var(--success-lighter);
}

.Switch-wrapper--checkedDisabled.Switch-wrapper--warning {
    background-color: var(--warning-lighter);
}

.PopperWrapper-trigger {
    cursor: pointer;
}

.Dropdown {
    min-width: unset !important;
}

/** Table **/
.Table {
    background: transparent;
    box-shadow: none;
}

.bp3-table-quadrant {
    background: transparent;
}

.bp3-table-column-headers {
    background: transparent;
}

.Table-headerCell {
    display: flex;
    align-items: center;
}

.Table-menuWrapper {
    opacity: unset;
    position: unset;
    display: flex;
